import store from '../../../store/index'
import { DataServer } from './data-server'
import * as viewer from '../../Features/helpers/loadFeature'
import * as loadAquaria from '../../../utils/loadData'
import * as keyboard from '../../../utils/keyboard'
var alignment = new jolecule.AquariaAlignment()
export var joleculeAlignment = alignment
export var attributes
var newAttachToDiv = '#threeDSpan-inner'
export var embededJolecule

export function initialisePanel3d () {
    embededJolecule = jolecule.initEmbedJolecule({
        divTag: newAttachToDiv,
        isLoop: false,
        isGrid: true,
        isLegend: true,
        backgroundColor: '#cccccc',
        isEditable: true,
        isResidueSelector: false,
        isExtraEditable: true,
        isMenu: true,
        isToolbarOnTop: true,
        isToolbarOn: true,
        isTextOverlay: false,
        isMouseWheel: true
    })
    window.AQUARIA.panel3d = embededJolecule
    document.addEventListener('keydown', keyboard.onkeydown)
}

export function load (attributes) {
    reload(attributes)
}
  
export function reload (attributes) {
    attributes = attributes
    store.commit('setStructureAttributes', attributes)
    // seqId = attributes.sequences[0].primary_acccession
    if (attributes.pdb_id !== embededJolecule.soup.structureId) {
      embededJolecule.clear()
    }

    // prevent chain switching from Jolecule inadvertently
    // telling Aquaria to switch proteins when loading and
    // the default chain is not the first
    joleculeAlignment.selectNewChain = () => {}
    store.commit('showLoader', true)
    let PDB_URI = ''
    var group = ''
    // Would need to set dynamic format in dataServer.getProteinData() in jolecule to support mmcif
    // PDB_URI = 'https://files.rcsb.org/download/1ATN.cif'
    if (attributes.biounit == 0) {
        group = attributes.pdb_id[1] + attributes.pdb_id[2]
        PDB_URI = `https://data.pdbj.org/pub/pdb/data/structures/divided/pdb/${group}/pdb${attributes.pdb_id}.ent.gz`
    } else {
        group = attributes.pdb_id[1] + attributes.pdb_id[2]
        PDB_URI = `https://data.pdbj.org/pub/pdb/data/biounit/coordinates/divided/${group}/${attributes.pdb_id}.pdb${attributes.biounit}.gz`
    }
    console.log(PDB_URI)
    const dataServer = new DataServer(PDB_URI, attributes.pdb_id)
    embededJolecule.asyncAddDataServer(
        dataServer
    )
    .then(function () {
      AQUARIA.panel3d.soupWidget.click = mouseClick
      // AQUARIA.panel3d.soupWidget.updateHover = updateHover
      AQUARIA.panel3d.widget.sequence.hover = new PopupText(`#threeDSpan-inner-sequence-widget-inner`, 15)
      if (store.state.structure.accession) {
        setAlignment(attributes)
      } else {
        setAlignmentforPDB(attributes)
      }
    })
    .then(function () {
      blankApplet(false)
    })
}

export function generateAttributes (threeDWidth, threeDHeight, pdb_id, pdb_chain, biounit, sequences, common_names, pssh_alignment, transform,
    conservations) {
    var instanceId = sequences[0].primary_accession + '-' + pdb_id + '-' + (store.state.structure.chain ? store.state.structure.chain : pdb_chain[0])
    return {
      width: threeDWidth,
      height: threeDHeight,
      instanceId,
      biounit,
      pdb_id,
      pdb_chain,
      common_names,
      transform,
      alignment: pssh_alignment,
      conservations,
      sequenceAlignments: null,
      sequences
    }
  }

export function setAlignment (attributes) {
  // console.log('JoleculePanel.setAlignment', attributes)
  joleculeAlignment.reload(attributes, embededJolecule)
  joleculeAlignment.selectNewChain = function (seqId, seqName, pdbId, chain) {
    if (seqId && !(seqId === store.state.structure.accession && chain === store.state.structure.chain) && store.state.structure.accession) {
      // console.log('JoleculePanel.setAlignment.selectNewChain', seqId, seqName, chain)
      loadAquaria.chainSelected(seqId, pdbId, chain, attributes)
    }
  }
}

export function setAlignmentforPDB (attributes) {
  /** Call this function when PDB loaded without a user-defined sequence */
  joleculeAlignment.reload(attributes, embededJolecule)
  AQUARIA.panel3d.controller.clickBackground()
  joleculeAlignment.selectNewChain = function (seqId, seqName, pdbId, chain) {
    if (seqId && !(seqId === store.state.structure.accession && chain === store.state.structure.chain) && store.state.structure.accession) {
      // console.log('JoleculePanel.setAlignment.selectNewChain', seqId, seqName, chain)
      loadAquaria.chainSelected(seqId, pdbId, chain, attributes)
    }
  }
}

export function buildFeatures (featureNames, featureDescriptions, featurePositions, featureColours) {
    const features = []
    for (var i = 0; i < featurePositions.length; i++) {
        var featurePosition = featurePositions[i].split(':')
        var featureStart = parseInt(featurePosition[0])
        var featureEnd = parseInt(featurePosition[1])
        for (var j = featureStart; j <= featureEnd; j++) {
        if (featureDescriptions[i].length > 0) {
          features.push({
            Residue: j,
            Color: featureColours[i],
            Name: featureNames[i].replace(/\<[^\>\<]*\>/g, '').replace(/^.*\:/, ''),
            Description: '<br/>Description: ' + featureDescriptions[i]
          })
        } else {
          features.push({
            Residue: j,
            Color: featureColours[i],
            Name: featureNames[i].replace(/\<[^\>\<]*\>/g, '').replace(/^.*\:/, '')
          })
        }
        }
    }
    return features
}

export function addAnnotation (id, annotationName, featureColours, featureNames, featureDescriptions, featurePositions, featureURLs, featureURLTexts) {
    const features = buildFeatures(featureNames, featureDescriptions, featurePositions, featureColours)
    joleculeAlignment.colorFromFeatures(embededJolecule, features, id, annotationName)
}
  
export function removeAnnotation (id, annotationName) {
    joleculeAlignment.colorFromConservation(embededJolecule)
}

/** Popup on hover function for Structure */
var updateHover = function () {
    AQUARIA.panel3d.widget.sequence.hover()
    let featureRes = false
    this.iAtomHover = this.getIAtomHover()
    if (this.iAtomHover) {
      let atom = this.soup.getAtomProxy(this.iAtomHover)
      let iRes = atom.iRes
      let residue = this.soup.getResidueProxy(iRes)
      let [seqName, seqResNum, c] = joleculeAlignment.mapPdbResOfChainToSeqRes(
        residue.structureId,
        residue.chain,
        residue.resNum
      )
      if (joleculeAlignment.features) {
        joleculeAlignment.features.forEach(function (feature) {
          if (seqResNum === feature.Residue) {
            featureRes = true
          }
        })
      }
      // this.popupText(this.iAtomHover).split('Structure')[0]
      let pos = this.soup.getAtomProxy(this.iAtomHover).pos
      let vector = this.getPosXY(pos)
      vector.height = 0
      vector.width = 0
      if (featureRes && store.state.variantResidues[seqResNum]) {
        let popupText = store.state.variantResidues[seqResNum]
        store.commit('setPopupText', popupText)
        store.commit('setVariantIdx', 0)
        store.commit('setVariant', seqResNum)
      } else {
        let pdb = residue.resId.split(':')[0]
        let Aaidx = store.state.oneAaCodes.indexOf(c)
        let longAa = store.state.threeAaCodes[Aaidx]
        let ptitle = '<span style="font-weight: 700;">' + seqName + '</span> sequence: ' + longAa + '–' + seqResNum
        var ptext = pdb + '-' + residue.chain + ' structure: ' + residue.resType + '-' + residue.resNum
        if (document.querySelector('[id^="residue_' + seqResNum)) {
          ptext = ptext + '</br> Structures with residue : '
          let totalPdbStruct = document.querySelector('[id^="residue_' + seqResNum).id.split('_')[3]
          store.commit('setTotalPDBres', totalPdbStruct)
        } else {
          store.commit('setTotalPDBres', null)
        }
        store.commit('setPopupTitle', ptitle)
        // ptext = ptext + '</br> Atom: ' + atom.atomType
        store.commit('setPopupText', ptext)
      }
      store.commit('setPopupCoordinates', vector)
      store.commit('setPopupType', 'structure')
    } else {
      if (store.state.popupType !== 'coveragemap') {
        var closeBtn = document.getElementById('x_close')
        closeBtn.click()
      }
    }
}

/** Popup on hover class for Sequence bar */
class PopupText {
  constructor (divTag, heightArrow) {
      if (_.isUndefined(heightArrow)) {
          this.heightArrow = 30
      } else {
          this.heightArrow = heightArrow
      }
      this.parentDiv = $(divTag)
  }

  move (x, y) {
      if (
          x < 0 ||
          x > this.parentDiv.width() ||
          y < 0 ||
          y > this.parentDiv.height()
      ) {
          this.hide()
          return
      }

      let vector = {}
      vector.x = x - 50
      vector.y = document.querySelector('.jolecule-embed-footer').getBoundingClientRect().y + y + (100 - this.heightArrow - 5)
      vector.width = 100
      vector.height = 100
      store.commit('setPopupCoordinates', vector)
  }

  hide () {
    store.commit('setPopupCoordinates', null)
    store.commit('setPopupType', null)
  }

  html (text) {
    let featureRes = false
    text = text.split('<br>')
    let ptitle = text[0]
    let popupText = text[1]
    let seqResNum = parseInt(ptitle.match(/\d+$/)[0])
    popupText = popupText + '</br> Structures with residue ' + seqResNum + ' : '
    let totalPdbStruct = document.querySelector('[id^="residue_' + seqResNum).id.split('_')[3]
    if (joleculeAlignment.features) {
      joleculeAlignment.features.forEach(function (feature) {
        if (seqResNum === feature.Residue) {
          featureRes = true
        }
      })
    }
    if (featureRes && store.state.variantResidues[seqResNum]) {
      let popupText = store.state.variantResidues[seqResNum]
      store.commit('setPopupTitle', store.getters.getLoadedFeature.set.Tracks[0][0].label)
      store.commit('setPopupText', popupText)
      store.commit('setVariantIdx', 0)
      store.commit('setVariant', seqResNum)
    } else {
      store.commit('setPopupTitle', text[0])
      store.commit('setTotalPDBres', totalPdbStruct)
      store.commit('setPopupText', popupText)
    }
  }

  remove () {
      this.div.remove()
      this.arrow.remove()
  }
}

var mouseClick = function () {
  console.log("Jolecule click overridden")
  let iAtomPressed = this.iAtomHover
  let iResPressed = this.soup.getAtomProxy(iAtomPressed).iRes
  if (exists(iResPressed) && iResPressed === this.iResFirstPressed) {
    store.commit('setResidueSelected', true)
    store.commit('setPopupCoordinates', null)
    if (!store.state.structure.accession) { // select a chain from the structure
      store.commit('setResidueSelected', false)
      loadAquaria.selectChainwithiRes(iResPressed)
    }
    else if (!event.metaKey && !event.shiftKey && this.soup.isSameChainSelected(iResPressed)) {
      console.log('Got here')
      this.controller.triggerAtom(iAtomPressed) // selects residue
    } else if (event.metaKey) {
        this.controller.toggleSelectResidue(iResPressed)
        this.controller.zoomToSelection()
        // Disable clipping planes and fog
    } else if (event.shiftKey) {
        this.controller.selectAdditionalRangeToResidue(
            this.iResFirstPressed
        )
        this.controller.zoomToSelection()
        // Disable clipping planes and fog
    } else {
        this.controller.selectAdditionalResidue(this.iResFirstPressed)
    }
    if (this.soupView.currentView.show.transparent) { // select a NEW chain from the structure
        if (!this.soup.isSameChainSelected(iResPressed) && !event.metaKey && !event.shiftKey) {
            let chain = this.soup.getResidueProxy(iResPressed).chain
            let iStructure = this.soup.getResidueProxy(iResPressed).iStructure
            this.controller.selectChain(iStructure, chain)
            store.commit('setResidueSelected', false)
        } else if (!this.soup.isSameChainSelected(iResPressed)) {
            this.controller.selectTraceOfResidue(iResPressed)
        }
    }
  } else if (this.soup.residueStore.selected.includes(1)) {
    store.commit('setResidueSelected', false)
    this.controller.clearSelectedResidues()
    let selectediRes = this.soup.selectedTraces.map(i => this.soup.traces[i]).map(t => this.soup.getResidueProxy(t.indices[0]))
    let chain = selectediRes[0].chain
    let iStructure = selectediRes[0].iStructure
    this.controller.selectChain(iStructure, chain)
  }
  else {
    store.commit('setResidueSelected', false)
    let pdb = this.soup.structureId
    this.controller.clearSelectedResidues()
    this.controller.clickBackground()
    if (pdb.includes('[')) {
      pdb = pdb.split('[')[0]
    }
    store.commit('setStructure', { accession: null, pdb: pdb, chain: null })
    store.commit('setSecondaryStructure', null)
    store.commit('setGene', null)
    store.commit('setOrganism', null)
    store.commit('setCurrentSequence', null)
    store.commit('setUrlParams', window.location.search)
    store.commit('setCoverageMap', false)
    store.commit('setLoadedFeature', {set: null, track: null})
    store.commit('setPopupCoordinates', null)
    viewer.removeFeature()
    history.pushState(null, document.title, '/' + pdb)
    // loadAquaria.loadPDB(pdb)
  }
  console.log('clear press')
  this.iAtomFirstPressed = null
  this.iResFirstPressed = null

  this.getPointer(event)
  this.updateHover()
}

function exists (x) {
  return !_.isNil(x)
}

export function blankApplet (isOn, message) {
  if (isOn) {
    var appletMessage = $('#waitingFrame').contents().find('#appletMessage')
    if (message) {
      appletMessage.html(message)
    } else {
      appletMessage.text('Please wait...')
    }
    if (!$('#waitingFrame').is(':visible')) {
      $('#waitingFrame').hide()
      $('#waitingFrame').fadeIn('fast')
    }
  } else {
    $('#waitingFrame').fadeOut('slow')
    store.commit('showLoader', false)
  }
}

export function changeViewerSize (w, h) {
  embededJolecule.resize()
}
